* {
  padding: 0;
  margin: 0; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  /* stylelint-disable-line */
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: 'Georgia', serif;
  font-size: 16px; }

main {
  max-width: 800px;
  margin: 30px auto;
  padding: 0 20px; }

header h1 {
  margin-bottom: 30px; }
